.create-new-team-page .title {
  margin-top: 0.9375rem;
  margin-left: 0.9375rem;
  font-size: 1.125rem;
  font-weight: 400;
  color: #ffffff;
}
.create-new-team-page .create-input {
  display: flex;
  align-items: center;
  margin-top: 1rem;
  height: 2.5rem;
}
.create-new-team-page .create-input .icon {
  margin-left: 0.84375rem;
  font-size: 0.75rem;
  color: #FF3333;
}
.create-new-team-page .create-input .input-title {
  margin-left: 0.625rem;
  font-size: 0.75rem;
  color: #ffffff;
}
.create-new-team-page .create-input .input {
  margin-left: 0.625rem;
  width: 15rem;
  height: 2.5rem;
  line-height: 2.5rem;
  position: relative;
  background: rgba(255, 255, 255, 0);
}
.create-new-team-page .create-input .input:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #fff;
  color: #fff;
  transform-origin: 0 100%;
  transform: scaleY(0.5);
}
.create-new-team-page .create-input .input .am-list-item {
  position: relative;
  background: rgba(255, 255, 255, 0);
  padding-left: 0 !important;
}
.create-new-team-page .create-input .input .am-list-item .am-list-line .am-input-extra {
  position: absolute;
  top: 0.875rem;
  right: 0;
  font-weight: 400;
  color: #ffffff;
}
.create-new-team-page .create-input .input input {
  font-size: 0.75rem;
  background: rgba(255, 255, 255, 0);
  width: 14.0625rem;
  border: none;
  color: #ffffff;
}
.create-new-team-page .descript {
  padding-left: 0.9375rem;
  padding-top: 0.625rem;
  line-height: 1.25rem;
}
.create-new-team-page .descript .des-tip {
  display: flex;
  justify-content: flex-start;
  padding-left: 1.5625rem;
  font-size: 0.5625rem;
  color: #ffffff;
}
.create-new-team-page .descript .des-tip .red {
  color: #FF3333 !important;
}
.create-new-team-page .descript .des-tip .blue {
  color: #00D2FF;
}
.create-new-team-page .footerButton {
  margin-top: 2.5rem;
  padding-left: 1.5625rem;
  padding-right: 1.5625rem;
}
.create-new-team-page .warn {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #FF3333;
  height: 1.875rem;
  font-size: 0.5625rem;
}
